*{
    margin:0;
    padding:0;
    user-select: none;

    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.sidebar-left{
position:fixed;
width: 250px;
height: 100%;
left: 0;
background: #1b1b1b;
}

.sidebar-right{
    position:fixed;
    width: 250px;
    height: 100%;
    left: 0;
    background: #1b1b1b;
    }

.sidebar .text{
    color: white;
    font-size: 25px;
    font-weight: 600;
    line-height: 65px;
    text-align: center;
    background: #1e1e1e;
    letter-spacing: 1px;
}

nav ul{
    background:rgb(15, 14, 14);
    height: 100%;
    width:100%;
    list-style: none;
}

nav ul li{
    line-height:60px;
    border-bottom: 1px solid rgba(255,255,255,0.1)
}

nav ul li a{
    color: white;
    text-decoration: none;
    font-size: 18px;
    padding-left: 40px;
    font-weight: 500;
    display: block;
    width: 100%;
    border-left: 3px solid transparent;

}

/* Format changes if you are mouse-hovering! */
nav ul li a:hover{
    color: cyan;
    border-left-color: cyan ;

}